import {
  Component,
  HostBinding,
  Input,
  OnDestroy,
  OnInit
} from '@angular/core';
import { Acting } from '@sofico-framework/utils';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';

@UntilDestroy()
@Component({
  selector: 'sof-centered-spinner-on-acting',
  template: `
    <div class="base" [class.show]="show">
      <div class="overlay" (click)="onClick($event)"></div>
      <sof-loading></sof-loading>
    </div>
  `,
  styleUrls: ['./centered-spinner-on-acting.component.scss']
})
export class CenteredSpinnerOnActingComponent implements OnInit, OnDestroy {
  @Acting() acting$;
  @Input() noClickThrough = false;
  @HostBinding('class.block') get showBinding(): boolean {
    return this.show && this.noClickThrough;
  }

  show = false;

  ngOnInit(): void {
    this.acting$
      .pipe(takeUntilDestroy(this))
      .subscribe(acting => (this.show = acting));
  }

  ngOnDestroy(): void {}

  onClick(event: MouseEvent): void {
    event.stopPropagation();
  }
}
